/* Home Container */
body {
    font-family: garamond;
    background-image: url('../images/forest.jpg'); /* Path to forest image */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; /* Prevent background image from repeating */
    height: 100vh; /* Ensure the body covers the entire viewport height */
  }
  
  h2 {
    margin-bottom: 30px;
    text-align: center;
    color: #3b6969;
  }
  h3{
    margin-bottom: 30px;
    text-align: center;
    color: #3b6969;
  }
  p
    {
        line-height: 1;
    }
  
  /* Form Group */
  .form-group {
    margin-bottom: 20px;
  }
  
  /* Input Fields */
  input[type="text"],
  input[type="email"],
  input[type="password"],
  input[type="tel"],
  textarea[type="text"] {
    width: 100%;
    padding: 15px;
    border: 1px solid #92afb5;
    border-radius: 10px;
    font-size: 10px;
    background-color: rgba(255, 255, 255, 0.95); /* Adjust background transparency */
    box-sizing: border-box; /* Include padding and border in element's total width and height */
  }
  
  /* Submit Button */
  button[type="submit"] {
    padding: 15px 30px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 100%; /* Set button width to fill container */
  }
  
  button[type="submit"]:hover {
    background-color: #388e3c;
  }
  
  /* Error Message */
  .error-message {
    color: #dc3545;
    margin-top: 10px;
    font-size: 14px;
  }
  
  /* Navigation */
  .navbar {
    background-color: #77B0AA;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navbar-left {
    padding-left: 20px;
  }
  
  .navbar-right {
    padding-left: 500px;
    padding-right: 20px;
  }
  
  .navbar-brand {
    color: #333;
    font-size: 25px;
    font-weight: bolder;
    text-decoration: none;
  }
  
  .nav-link {
    margin-left: 20px;
    color: #333;
    text-decoration: none;
  }
  
  /* Top Banner */
  /* Top Banner */
.top-banner {
    position: relative;
    width: 100%;
    height: 93vh; /* Set height to half of window height */
    overflow: hidden;
  }
  
  .image {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensure the image covers the entire container */
  }
  
  

  /* Recent Blogs Section */
  .blogs-section {
    background-color:#E3FEF7;
    position: relative;
    width: 100%;
    height: 50vh; /* Set height to half of window height */
    overflow: hidden;
  }
  .contact-message
{
    width: 40%;
    margin: 40px auto 60px;
}
  /* Contact Us Section */
  .contact-section {
    text-align: center;
    background-color: #E3FEF7;
    position: relative;
    width: 100%;
    height: 50vh; /* Set height to half of window height */
    overflow: hidden;
  }
  
  .contact-section p {
    margin-bottom: 10px;
  }
  
  .contact-section button {
    padding: 15px 30px;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .contact-section button:hover {
    background-color: #388e3c;
  }
  
  /* Footer */
  .footer {
    background-color: #135D66;
    
    text-align: center;
    padding: 20px 0 10px;
  }
  .copyright
  {
    color: #EAF6F6;
    font-size: 0.75rem;
    padding: 10px 0px;
  }
  .footer-link{
    color: white;
    text-decoration: none;
    padding-left: 20px;
  }